import React from 'react';
import { Select } from 'antd';

const SelectAntdComponent: React.FC = () => (
    <>
        <Select
            defaultValue="1"
            style={{ width: 170}}
            options={[
                {
                    value: '1',
                    label: '',

                },
                {
                    value: '2',
                    label: ' ',
                },
            ]}
        />

    </>
);

export default SelectAntdComponent;